import React from 'react'
import style from './reusableComponents.module.css'
import arrowTable from '../../Images/arrowTable.png'

/**
 * TableHeader - компонент для отображения заголовков таблицы с поддержкой сортировки.
 *
 * @param {Array} tableArray - Массив объектов с данными для заголовков.
 * @param {Function} toggleSort - Функция для обработки сортировки.
 * @param {string} sortField - Текущее поле для сортировки.
 * @param {boolean} sortDirection - Направление сортировки (true - по возрастанию, false - по убыванию).
 * @param {number} tableColumns - Количество колонок в таблице.
 */

const TableHeader = (props) => {
  const { tableArray, toggleSort, sortField, sortDirection, tableColumns } =
    props

  return (
    <div
      className={style.tableHeader}
      style={{ gridTemplateColumns: `repeat(${tableColumns}, 1fr)` }}
    >
      {tableArray.map(({ label, field, image }) => (
        <div
          className={style.tableParametersSort}
          onClick={() => toggleSort(field)}
          key={field}
        >
          <p
            className={style.tableParametersText}
            style={sortField === field ? { textDecoration: 'underline' } : {}}
          >
            {label}
          </p>
          {image && (
            <img
              style={
                sortField === field
                  ? sortDirection
                    ? {}
                    : { transform: 'rotate(180deg)' }
                  : {}
              }
              src={arrowTable}
              alt={'img'}
              className={style.tableParametersImg}
            />
          )}
        </div>
      ))}
    </div>
  )
}

export default TableHeader
